export default {
  modules: {
    navigator: {
      strict: true,
      namespaced: true,
      state: {
        stack: [],
        options: {},
        books: [{
          id: 1,
          name: '1',
          borrower: '1',
          returnDate: '2018/07/05'
        }, {
          id: 2,
          name: '2',
          borrower: '2',
          returnDate: '2018/07/06'
        }, {
          id: 3,
          name: '3',
          borrower: '3',
          returnDate: '2018/07/07'
        }, {
          id: 4,
          name: '4',
          borrower: '4',
          returnDate: '2018/07/08'
        }, {
          id: 5,
          name: '5',
          borrower: '5',
          returnDate: '2018/07/09'
        }, {
          id: 6,
          name: '6',
          borrower: '6',
          returnDate: '2018/07/10'
        }]
      },
      mutations: {
        push(state, page) {
          state.stack.push(page);
        },
        pop(state) {
          if (state.stack.length > 1) {
            state.stack.pop();
          }
        },
        replace(state, page) {
          state.stack.pop();
          state.stack.push(page);
        },
        reset(state, page) {
          state.stack = [page || state.stack[0]];
        },
        options(state, newOptions = {}) {
          state.options = newOptions;
        }
      }
    },

    splitter: {
      strict: true,
      namespaced: true,
      state: {
        open: false
      },
      mutations: {
        toggle(state, shouldOpen) {
          if (typeof shouldOpen === 'boolean') {
            state.open = shouldOpen;
          } else {
            state.open = !state.open;
          }
        }
      }
    },

    tabbar: {
      strict: true,
      namespaced: true,
      state: {
        index: 1
      },
      mutations: {
        set(state, index) {
          state.index = index;
        }
      }
    }
  }
};



