<template>
  <v-ons-page>
    <v-ons-toolbar>
      <v-ons-list-item :modifier="md ? 'nodivider' : ''">
        <div class="left">
          <v-ons-icon icon="fa-search" class="list-item__icon"></v-ons-icon>
        </div>
        <label class="center">
          <v-ons-input float maxlength="20"
                       placeholder=""
          >
          </v-ons-input>
        </label>
      </v-ons-list-item>
    </v-ons-toolbar>
    <v-ons-list>
      <v-ons-list-header></v-ons-list-header>
      <!--<v-ons-list-item v-for="animation in animations" :key="animation"-->
        <!--modifier="chevron"-->
        <!--@click="transition(animation)"-->
      <!--&gt;-->
        <!--{{ animation }}-->
      <!--</v-ons-list-item>-->
      <v-ons-list-item v-for="book in books" :key="book.id"
                       modifier="chevron"
                       @click="transition(book)"
      >
        {{ book.name }}
      </v-ons-list-item>
    </v-ons-list>
  </v-ons-page>
</template>

<script>
const transitionPage = {
  template: `
    <v-ons-page>
      <custom-toolbar backLabel="Anim">
        {{ animation }}
      </custom-toolbar>
      <p style="text-align: center">
        Use the VOnsBackButton
      </p>
    </v-ons-page>
    `
};
import BookPage from './Book.vue';
export default {
  data() {
    return {
      animations: ['none', 'default', 'slide-ios', 'slide-md', 'lift-ios', 'lift-md', 'fade-ios', 'fade-md']
    };
  },
  computed: {
    books() {
      return this.$store.state.navigator.books;
    }
  },
  methods: {
    transition(book) {
      this.$store.commit('navigator/options', {
        // Sets animations
        // animation: book.name,
        animation: 'default',
        // Resets default options
        callback: () => this.$store.commit('navigator/options', {})
      });

      this.$store.commit('navigator/push', {
        // extends: transitionPage,
        extends: BookPage,
        data() {
          return {
            name: book.name,
            borrower: book.borrower,
            returnDate: book.returnDate
          }
        }
      });
    }
  }
};
</script>



